.signIn-pageCntnr {
    min-height: 100vh;
    width: 100vw;
    /* border: 0.5px solid red; */
    background-color: #EAEDED;
    display: flex;
    flex-direction: column;
    align-items: center;
}


.signIn-page_headerCntnr {
    height: 90px;
    width: 100%;
    /* border: 0.5px solid brown; */
    /* background-color: #EAEDED; */
    display: flex;

}
.signIn-page_logoCntnr {
    height: fit-content;
    margin: auto;
    text-decoration: none;
    /* border: 0.5px solid green; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.signIn-page_logoCntnr .logo {
    margin-top: .8rem;
    font-size: 3.2rem;
    letter-spacing: -0.15rem;
    color: transparent;
    /* border: 0.5px solid red; */
    background: linear-gradient(90deg, white, rgb(0, 119, 119), white, rgb(0, 119, 119), white );

    background-clip: text;
    animation: movinggradient 5s ease infinite;
    background-size: 200%;
}
@keyframes movinggradient {
    0% { background-position: 0% 25%; }

    25% { background-position: 25% 50%; }

    50% { background-position: 50% 25%; }

    25% { background-position: 25% 0%; }

    0% { background-position: 0% 25%; }
}

.signIn-page_logoCntnr .logo-withme {
    letter-spacing: .2rem;
    color: black;
    position: relative;
    left: 2rem;
    top: -0.25rem;
    /* border: 0.5px solid red; */
}

.formCntnr {
    width: 353px;
    margin-top: 10px;
    /* border: 0.5px solid red; */

}

.signIn_form {
    height: 280px;
    width: 100%;
    padding: 20px;
    padding-top: 10px;
    border-radius: 3px;
    border: 0.3px solid black;
    background-color: white;
}

.signIn_form h3 {
    margin-bottom: 15px;
    font-size: 2.8rem;
    font-weight: 400;
    /* border: 0.5px solid red; */
}
.signIn_form label {
    font-size: 1.5rem;
    font-weight: 500;
}

.signIn_form input {
    height: 28px;
    width: 70%;
    margin-bottom: 15px;
    padding-left: 8px;
    font-size: 1.5rem;
    border-radius: 3px;
    border: 0.5px solid black;
}
.signIn_form .password {
    font-size: 1.9rem;
}
.signIn_form .signIn {
    height: 35px;
    width: 100%;
    margin-top: 35px;
    font-size: 1.8rem;
    cursor: pointer;
    color: black;
    border: 0.3px solid rgb(163, 163, 163);
    background-color: lightgray;
}
.signIn_form .signIn:hover {
    background-color: #EAEDED;
}

.newTo_shoppingwithmeCntnr {
    width: 100%;
    margin-top: 55px;
    /* border: 0.5px solid blue; */
    display: flex;
    justify-content: center;
    align-items: center;
}

.sideOf-shoppingwithme {
    height: 0.5px;
    width: 92px;
    background-color: gray;
}

.newTo_shoppingwithme {
    margin: 0 5px;
    font-size: 1.3rem;
}

.create_anAccount {
    height: 35px;
    width: 100%;
    margin-top: 20px;
    font-size: 1.8rem;
    cursor: pointer;
    border-radius: 3px;
    border: 0.3px solid rgb(163, 163, 163);
    background-color: lightgray;
}
.create_anAccount:hover {
    background-color: #EAEDED;
}


/* //////// css for mobile screen */
@media (max-width: 395px) {

    .signIn_form .signIn { cursor: none; }
    .create_anAccount { cursor: none; }
}